@import "_variables";

$interview-header-height: $header-height + 74px;

.header.interview-header {
  height: $interview-header-height;
  padding-bottom: 0;

  @include setup(
    $backgroundColor: map-get($colors, "black"),
    $backgroundImage: "/images/tile_blk.gif",
  );

  .interview-header-title {
    height: $header-height;
    // In the old frameset, vertical margins between adjacent framesets were
    // collapsed. To mimic this behavior, a negative margin is used.
    margin-bottom: -4px;
  }
}

// Override specificity of .main
.root .main {
  padding-top: $interview-header-height + $main-padding;
  @include setup($backgroundImage: "/images/tile_blk.gif");
}
